<template>
  <navbar
    class="p-0"
    position="fixed"
    type="primary"
    :transparent="false"
    :color-on-scroll="colorOnScroll"
    menu-classes="ml-auto"
  >
    <template slot-scope="{ toggle, isToggled }">
      <el-popover
        ref="popover1"
        popper-class="popover"
        placement="bottom"
        width="200"
        trigger="hover"
      >
        <div class="popover-body"></div>
      </el-popover>
    </template>

    <template slot="navbar-menu">
      <router-link slot="brand" class="navbar-brand mr-lg-5" to="/">
        <img src="img/logo.png" class="logo-header main-logo" alt="logo" />
      </router-link>

      <li class="nav-item">
        <nav-link to="/" class="nav-link top-menu">
          <i class="now-ui-icons education_paper"></i>
          {{ $t("home") }}
        </nav-link>
      </li>
      <li class="nav-item">
        <nav-link to="/services" class="nav-link top-menu">
          <i class="now-ui-icons business_chart-pie-36"></i>
          {{ $t("services") }}
        </nav-link>
      </li>
      <li class="nav-item">
        <nav-link to="/alliances" class="nav-link top-menu">
          <i class="now-ui-icons business_globe"></i>
          {{ $t("alliances") }}
        </nav-link>
      </li>
      <li class="nav-item">
        <nav-link to="/contact" class="nav-link top-menu">
          <i class="now-ui-icons users_circle-08"></i>
          {{ $t("contact") }}
        </nav-link>
      </li>
      <li class="nav-item float-right menu-languages">
        <a
          class="nav-link language-item"
          v-for="entry in languages"
          :key="entry.title"
          @click="changeLocale(entry.language)"
          v-bind:title="entry.title"
          data-placement="bottom"
          >{{ entry.title }}</a
        >
      </li>

      <a
        class="navbar-brand mr-lg-5"
        href="https://bindermexico.com"
        target="_blank"
        ><img
          src="img/logo-binder.png"
          class="logo-header logo-binder"
          alt="logo"
        />
      </a>
    </template>
  </navbar>
</template>

<script>
import { DropDown, NavbarToggleButton, Navbar, NavLink } from "@/components";
import { Popover } from "element-ui";
import i18n from "@/plugins/i18n";
export default {
  name: "main-navbar",
  props: {
    transparent: Boolean,
    colorOnScroll: Number,
  },
  components: {
    DropDown,
    Navbar,
    NavbarToggleButton,
    NavLink,
    [Popover.name]: Popover,
  },
  data() {
    return {
      languages: [
        { flag: "us", language: "en", title: "English" },
        { flag: "es", language: "es", title: "Espaol" },
      ],
    };
  },
  methods: {
    changeLocale(locale) {
      i18n.locale = locale;
    },
    mounted() {
      console.log(this.props);
    },
  },
};
</script>

<style scoped>
.logo-header {
  width: 200px;
  max-width: 230px;
}

.main-logo {
  margin-right: 1rem;
}
.menu-languages {
  justify-content: flex-start;
  display: flex;
  margin-inline: 2em;
}

.top-menu,
.top-menu:hover {
  color: #fafafa;
}

.logo-binder {
  max-width: 260px;
  width: 260px;
  margin-left: 2rem;
}

.language-item {
  max-height: 40px;
  margin: 1rem 1rem 1rem 0rem !important;
}

.nav-item {
  display: flex;
  align-items: center;
}

.nav-open .logo-binder {
  max-width: 280px;
  width: 280px;
  margin-left: 0.7rem;
}

.nav-open .navbar-brand {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 0em !important;
  margin-bottom: 1em;
}
</style>
